<template>
    <div class="slideContent">
        <div class="content">
            <tabs :options={options}>
                <tab name="Енисей 2М">
                    <img class="product-img" src="src/assets/source.png" alt="Енисей 2М">
                    <div class="bottom-controls">
                        <router-link to="/complexes/eni2m" class="btnControls">
                            подробнее
                        </router-link>
                        <button class="btnControls">Видео</button>
                        <button class="btnControls">Фото</button>
                    </div>
                </tab>
                <tab name="Енисей 3D">
                    <img class="product-img" src="src/assets/Eni3D.png" alt="Енисей 2М">
                    <div class="bottom-controls">
                        <router-link to="/complexes/eni3d" class="btnControls">
                            подробнее
                        </router-link>
                        <button class="btnControls">Видео</button>
                        <button class="btnControls">Фото</button>
                    </div>
                </tab>
            </tabs>
        </div>
    </div>
</template>

<script>
export default {
  name: 'k-t-m-p-slide',
  data() {
    return {
      options: {
        useUrlFragment: false,
      },
    };
  },
};
</script>

<style lang="scss" scoped>

    .slideContent {
        background-color: #000;
        background-image: url(../../assets/ktmpBg.png);
        background-repeat: no-repeat;
        background-size: contain;
        background-position: center center;
        position: relative;
    }

    .content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 70%;
        height: 85%;
    }

    .tabs-component {
        width: 100%;
        height: 100%;
    }

    .product-img {
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .bottom-controls {
        position: absolute;
        bottom: 0;
    }

    .btnControls {
        background-color: transparent;
        border: 0;
        text-transform: uppercase;
        font-family: arame;
        font-weight: 600;
        letter-spacing: 3px;
        color: #fff;
        margin-right: 50px;
        cursor: pointer;

        &:last-child {
            margin-right: 0;
        }

        &:hover {
            text-decoration: underline;
        }
    }
</style>



